<template>
<div id="body">
  <div id="navbar">
    <div class="back"><i class="fa fa-arrow-left fa-lg" v-on:click="back"></i></div>
  </div>
  <div class="Content">
    <div v-html="title"></div>
    <div v-html="info"></div>
    <div v-html="content"></div>
    <div v-html="href"></div>
  </div>
  <yinyong v-bind:items="recommend"></yinyong>
</div>
  
</template>

<script>
// @ is an alias to /src
import yinyong from "@/components/yinyong.vue";
export default {
  name: "Content",
  data() {
    return {
      recommend: null,
      content: "",
      parmes:'',
      title:'',
      info:'',
      href:''
    };
  },
  components: {
    yinyong,
  },
  methods:{
    back(){
        this.$router.go(-1);//
    },

  },
  props: ["items"],
  created() {
    axios
      .get("https://www.apiopen.top/journalismApi")
      .then(res => {
        
        this.recommend = res.data.data.dy;        

          console.log(this.recommend);
      })
      .catch(err => {
        // console.error(err);
      });
  },
  created() {
    var parme=this.$route.query
    this.parmes = parme
    axios
      .get(
        `https://api.kele8.cn/agent/${this.parmes.id}`
      )
      .then(res => {
        // console.log(res);

        const cheerio = require("cheerio");
        const $ = cheerio.load(res.data);
        
       
        $("img").each(function(i, elem) {
          // console.log($(elem).data("src"));
          $(elem).attr("src", $(elem).data("src"));
        });
       
        this.content = $.html(".content");
        this.title= $.html(".title");
        this.info=$.html(".info");
        this.href=$.html(".href");
      })
      .catch(err => {
        console.error(err);
      });
  }
};
</script>
<style lang="less">
.title{
  letter-spacing: 1.5px;  
}
#navbar{
  position: fixed;
  top: 0;
  background: #ee1a1a;
  height: 40px;
  // width: ;
  line-height: 40px;
  color: white;
  z-index: 1;
  .back{
    background: url('https://static.ws.126.net/163/wap/f2e/milk_index/logo-site.png')no-repeat;
    background-position: center;
    background-size: 14%;
  }
}
.info{
  color: #888;
  font-size: 14px;
  font-weight: normal;
}
#body{
  height: 100%;
  position: absolute;
  width: 420px;
  top: 0px;
}
  .back{
    text-align: left;
    .fa-arrow-left{
      margin: 0 10px;
    }
  }
  img{
    width: 100%;
  }
  p{
    text-align: left;
    position: unset;
    font-size: 18px;
    text-indent: 2em;
    margin: 30px 0;
    line-height: 30px;
  }
  
  .Content{
    width: 100%;
    background: #eee;
    text-align: left;
    padding: 0 0 10px 0;
    div{
      margin: 0 3px;
    }
    .content{
      margin: 20px 0 0 0;
    }
    .photo{
      margin: 0;
      padding: 0;
      img{
        position: relative;
        left: -5px;
      }
    }
  }
  video{
    width: 100%;
  }
  .otitle{
    text-indent: 0;
    font-size: 16px;
  }
  h1{
    font-size: 20px;
    margin: 40px 0 10px 0;
    padding: 20px 0 0 0;
  }
  .editor{
    text-align: right;
    font-size: 16px;
  }
  font{
  text-indent: 0;
}
  

</style>