<template>
  <div class="CarItem" v-bind:data-path="item.link" v-on:click="handle">
    <div class="title-bar" v-bind:data-path="item.link">
      <span class="title" v-bind:data-path="item.link">{{item.title}}</span>
      <!-- <span>{{item.link}}</span> -->
      <div><span id="source">{{''+item.source}}</span></div>
    </div>
    <div class="img" v-bind:data-path="item.link" v-if="item.picInfo[0]">
      <img v-bind:src="item.picInfo[0].url" v-bind:data-path="item.link" v-if="item.picInfo[0]">
    </div>
  </div>
</template>

<script>
export default {
  name: "CarItem",
  props: ["item"],
  data() {
    return {};
  },
  components: {},
  methods: {
    handle(event) {
      console.log( event.target.dataset.path);
      this.$router.push({
        name: "Content",
        query: { id: event.target.dataset.path }
      });
    }
  }
};
</script>

