<template>
<div>
  <Car v-bind:items="recommend"></Car>
</div>

</template>
<script>

import Car from "@/components/Car.vue";
export default {
  name: "App",
  components: {
    Car,
  },
  data() {
    return {
      recommend: null,
      arr:[],
      itop:-80,
      imgW:40,
      left:-360
    };
  },
  methods:{
    mask(){
      this.left= -360;
    },
    handle(){
        // this.$router.replace('user');
      this.left= 0;
    },
    down(){
      this.isShowMb = false;
      this.imgW = 120;
      this.itop= 0;
    }
  },
  created() {
    axios
      // .get("https://www.apiopen.top/journalismApi")
      .get("journalismApi.json")
      .then(res => {
        
        this.recommend = res.data.data.tech;        

          console.log(this.recommend);
      })
      .catch(err => {
        // console.error(err);
      });
  },
  
};
</script>