<template>
<div>
    <Head v-bind:items="recommend"></Head>
</div>
</template>
<style lang="less">
  .stage{
    // width: 360px;
    overflow: hidden;
  }

</style>
<script>

import Head from "@/components/Head.vue";
export default {
  name: "App",
  components: {
    Head,
  },
  data() {
    return {
      recommend: null,
      imgW:40,
      itop:-80,
      left:-360
    };
  },
  methods:{
    mask(){
      this.left= -360;
    },
    handle(){
        // this.$router.replace('user');
      this.left= 0;
    },
    down(){
      this.isShowMb = false;
      this.imgW = 120;
      this.itop= 0;
    }
  },
  created() {
    axios
      // .get("https://www.apiopen.top/journalismApi")
      .get("journalismApi.json")
      .then(res => {
        
        this.recommend = res.data.data.toutiao;        

          // console.log(this.recommend);
      })
      .catch(err => {
        // console.error(err);
      });
  },
};
</script>
